import React, { Component } from 'react'
import 'bootstrap/dist/css/bootstrap.css';
import {HashRouter as Router , Switch , Route} from 'react-router-dom';
import Title from './Components/Title.js';
import update from 'react-addons-update';
import Menu from './Components/Menu.js';
import HomePage from './Components/HomePage.js';
import AddRoom from './Components/AddRoom.js';
import Room from './Components/Room.js';
import './App.css';

export default class App extends Component {
  state = {
    // rooms :[{name:"Room1" , color:"red" ,roomType:"Bedroom", products:[{device:"light",condition:"on"},{device:"Water Heater",condition:"off"}]},{name:"Room2" , color:"green" ,roomType:"Kitchen", products:[{device:"Air conditioner",condition:"off"},{device:"Water Heater",condition:"on"},{device:"light",condition:"on"}]}],
    rooms :[{}]
  }

  changeStatus = (x) =>{
    debugger;
    let item = this.state.rooms;
      item[0].products = {device:'light', condition:x};
      this.setState({rooms: item});
  }

    addRoom = (n,e,p) => {
      this.setState({rooms:[...this.state.rooms,{name:e , color:p ,roomType:n, products:[]}]});
    }
      // שלוש נקודות זה אומר תוסיף אוביקט חדש למערך שקיים
    //  console.log(`${id} ${info}`);
     
    addProduct = (index,typeOfProduct)=>{
      debugger;
      let tempProduct = {condition:false,device:typeOfProduct};
      this.state.rooms[index].products.push(tempProduct);
      this.setState({rooms:[...this.state.rooms]});
    }

    smartSwitch = (indexOfRoom,indexOfProduct)=>{
      this.state.rooms[indexOfRoom].products[indexOfProduct].condition = !this.state.rooms[indexOfRoom].products[indexOfProduct].condition ; 
      this.setState({rooms:[...this.state.rooms]});
    }

  render() {
          return (
    <div className="App">
    <Title />
    <Router>
        {/* <Menu /> */}
        <Switch>
            <Route exact path='/' component={()=>{return <HomePage  rooms = {this.state.rooms} />}} /> 
            {/* <Route exact path='/room' component={()=>{return <Room device={this.state.devices} name={this.state.rooms.name} roomType={this.state.rooms.roomType} newDevice={this.addDevice} />}} /> */}
          <Route exact path='/addroom' component={ () => {return <AddRoom newRoom={this.addRoom}  /> }}/>
           
          {this.state.rooms.map((element,index)=>{
                return(
                  <Route exact path={`/room${element.name}`} component={()=>{return <Room name={element.name} roomType={element.roomType} index={index} product={element.products} smartSwitch={this.smartSwitch} addProduct={this.addProduct}/>}} />
                )
              })}

        </Switch>
    </Router>
  </div>
);
}
}




