import React, { Component } from 'react'
import '../Style/Style.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import AddProducts from './AddProducts';

export default class Room extends Component {
    constructor(props){
        super(props);
        this.state={
            flage:false,
            device : '',
            condition:'',
            backgroundColor1:'red',
            backgroundColor2:'green'
        }
}

change = ()=>{
    this.setState({flag:!this.state.flag})
}


show = ()=>{
    if(this.state.flag == true){
        return (
            <div>
                <AddProducts index={this.props.index} addProduct={this.props.addProduct}/>
            </div>
        )
    }
    else{
        return (
            <div>
                  <button onClick={this.change}id="addDevice"className="btn btn-primary">add device</button>
                {/* <button onClick={this.change}>+</button> */}
            </div>
        )
    }
}
        

    

    render() {
        return (
            <div>  
                <div className="container">
                     <div className="row">
                     <div className="col-3">
                     {this.props.product.map((element,index)=>{

               let condition = element.condition?'green':'red';
               
               return (
                      
                    <button  style={{backgroundColor:condition}} id="deviceButton" className="btn btn-primary" onClick={()=>{this.props.smartSwitch(this.props.index,index)}}  > {element.device}</button>  
                )          
                })}
                </div> 

                <div id="room" className=" col-9 form-group">
                    <div className="col-xs-4">
                   Name room: {this.props.name}
                </div>
                <div className="col-xs-4">
                   Type room: {this.props.roomType}
                </div>
                
              {this.show()}

                </div>
                </div>
               
           </div>
           </div>
        )
    }
}





