import React, { Component } from 'react'
import {Link} from 'react-router-dom';
import '../Style/Style.css'
import 'bootstrap/dist/css/bootstrap.min.css'
export default class AddRoom extends Component {
  constructor(props){
    super(props);
    this.state={
      roomType:'',
       name:'',
      color:''
    }
}

selectRoomType = (n) =>{
  debugger;
  this.setState({roomType:n.target.value})
}

changeName = (e) =>{
this.setState({name:e.target.value})
}
changeColor = (p) =>{
this.setState({color:p.target.value})
}
 
useAddRoom = () => {
  debugger;
  this.props.newRoom(this.state.roomType,this.state.name,this.state.color);
}



  render() {
    return (
      <div className="containet">
      <div className="form-group">
        <div className="col-xs-4">
      <select id="selectRoom" onChange={this.selectRoomType}>
  <option>Select new room</option>
  <option>Bedroom</option>
  <option>Bathroom / Toilet</option>
  <option>Kitchen</option>
</select>
</div>
</div>
<div className="form-group">
  <div className="col-xs-4">
  <input Name="roomName" placeholder="Room name" maxLength="5" type="text" onChange={this.changeName} />
  </div>
</div>
<div className="form-group">
  <div className="col-xs-4">
  <input Name="roomColor" placeholder="Room color" type="text" onChange={this.changeColor}/>
  </div>
</div>
<div className="form-group">
  <div className="col-xs-4">
  <Link  to='/'> <button id="btnLogIn" onClick={this.useAddRoom} className="btn-primary">Add room</button></Link>
  </div>
</div>
    </div>
    )
  }
}



