import React, { Component } from 'react'
import {Link} from 'react-router-dom';
import '../Style/Style.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlusCircle } from '@fortawesome/free-solid-svg-icons'

export default class HomePage extends Component {
    constructor(props){
        super(props);
        

    }

    // chooseRoom = () => {
    //     debugger;
    //     this.props.currentRoom(this.state.index);
    // }

    render() {
        return (
            <div>
            {this.props.rooms.map((element)=>{
                return (
              <div  className="container">  
              <div className="row">      
                     <div  className="col-3 btn-group ">
            <Link to={`/room${element.name}`}> <button  id="roomButton"className="btn btn-default" style={{backgroundColor:element.color}}>{element.name} </button></Link>
              </div>
              </div>
              </div>
              )
    })}
            <div>
                <Link  to='/addroom'>< FontAwesomeIcon id="plus" className="fa-7x" icon={faPlusCircle} ></FontAwesomeIcon></Link>
            </div>
            
            </div>
        )}
    
    
}

