import React, { Component } from 'react'
import '../Style/Style.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlusCircle } from '@fortawesome/free-solid-svg-icons'
export default class AddProducts extends Component {
   
    constructor(props){
        super(props);
        this.state={
            type:'Lamp',
            condition:'off'
        }

    }
   
    selectType = (e)=>{
        console.log(e);
        
        this.setState({type:e.target.value});
    }
   
   
    render() {
        return (

            <div className="form-group">
            <div className="col-xs-4">
            <select id="selectDevice" onChange={this.selectType}>
  <option>Select new device</option>
  <option value="Air conditioner" >Air conditioner</option>
  <option value="Lamp">Lamp</option>
  <option value="Builer">Builer</option>
  <option value="Stereo">Stereo </option>
              </select>
            </div>
            <div className="col-xs-4">
            <button id="plusDevise" onClick={()=>{this.props.addProduct(this.props.index,this.state.type)}} className="btn btn-primery">< FontAwesomeIcon  className="fa-2x" icon={faPlusCircle} /></button>
            </div>
            </div>
            
        )
    }
}